.formControl{
    width: 90%;
    max-width: 40rem;
    margin: 2rem auto;
    padding: 2rem;
}

.control {
    margin: 1rem 0;
    display: flex;
    align-items: stretch;
    flex-direction: column;
  }
  
  
  .control label,
  .control input {
    display: block;
  }
  
  
  .control label {
    font-weight: bold;
    flex: 1;
    color: #5fe0e7;
    margin-bottom: 0.5rem;
  }
  
  
  .control input {
    flex: 3;
    font: inherit;
    padding: 0.35rem 0.35rem;
    border-radius: 6px;
    border: 1px solid #ccc;
  }

  .button{
    display: flex;
  }
  .button button:hover{
    background-color: aqua;
    color: blue;
    
  }
   .button button{
    width: 100px;
    margin:0 auto;
    border-radius: 5px;
   text-align: center;
   background-color: rgb(29, 100, 187);
   color: white;

  }

  .invalid input{
    background-color: pink;
  }